import React from 'react'
import {Routes, Route, Link} from 'react-router-dom'

//pages
import Product from './Product';

// swiper
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { Pagination, Navigation } from 'swiper/modules';

// styled
import styled from 'styled-components';

const Container = styled.section`
  width: 1280px;
  margin: 0 auto;
  overflow: hidden
`
const Title = styled.p`
  color: ${(props) => (props.textColor ? '#ec3029' : '#fff')};
  font-size: 30px;
  font-weight: 600;
  text-align: center;
  margin-top: 120px;
  margin-bottom: 40px
`
const Text = styled.p`
  font-size: 18px;
  text-align: center;
  color: #444
`
const FirstText = styled(Text)`
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 20px
`
const Concept = styled.div`
  width: 400px;
  margin-top: 60px;
  margin-right: ${(props) => (props.Right ? '0' : '40px')};
  float: left;
  p {
    font-size: 18px;
    font-weight: 500;
    margin-top: 14px;
    text-align: center;
    color: #444
  }
`
const Tap = styled.li`
  font-size: 20px;
  a {color: #fff}
`


export default function Home() {
  return (
    <>
      <Swiper
        slidesPerView={1}
        loop={true}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Pagination, Navigation]}
        className="mySwiper"
      >
      <SwiperSlide>
          <img src={process.env.PUBLIC_URL + '/images/visual_banner/visual_banner01.png'}/>
        </SwiperSlide>
        <SwiperSlide>
          <img src={process.env.PUBLIC_URL + '/images/visual_banner/visual_banner02.png'}/>
        </SwiperSlide>
        <SwiperSlide>
          <img src={process.env.PUBLIC_URL + '/images/visual_banner/visual_banner03.png'}/>
        </SwiperSlide>
        <SwiperSlide>
          <img src={process.env.PUBLIC_URL + '/images/visual_banner/visual_banner04.png'}/>
        </SwiperSlide>
        <SwiperSlide>
          <img src={process.env.PUBLIC_URL + '/images/visual_banner/visual_banner05.png'}/>
        </SwiperSlide>
      </Swiper>
      <Container>
        <Title textColor>OUR STORY</Title>
        <FirstText>   .</FirstText>
        <Text>    ,  ,    </Text>
        <Text>      .</Text>
        <Text>          </Text>
        <Text>      .</Text>
        <Concept>
          <img src={process.env.PUBLIC_URL + '/images/concept/concept01.jpg'}/>
          <p>#    </p>
        </Concept>
        <Concept>
          <img src={process.env.PUBLIC_URL + '/images/concept/concept02.jpg'}/>
          <p>#    </p>
        </Concept>
        <Concept Right>
          <img src={process.env.PUBLIC_URL + '/images/concept/concept03.jpg'}/>
          <p>#    </p>
        </Concept>
      </Container>
      <div style={{background: '#ec3029', marginTop: 130}}>
        <Container>
          <Title>OUR PRODUCT</Title>
        </Container>
        <ul style={{
           display: 'flex',
           gap: 50,
           justifyContent: 'center'
        }}>
          <Tap><Link to=''></Link></Tap>
          <Tap><Link to=''></Link></Tap>
          <Tap><Link to=''></Link></Tap>
        </ul>
        <Routes>
          <Route path=':product' element={<Product />} />
        </Routes>
      </div>
      
    </>
  )
}
